import { useState, useEffect, useRef, cloneElement } from 'react';
import { isString } from 'lodash/fp';

export const resolveGoogleMapsApiKey = () =>
    process.env.STORYBOOK_GOOGLE_MAPS_API_KEY;

export const isValueOption = value => {
    return (
        !isString(value) &&
        ((value?.code && value?.name) || (value?.id && value?.displayName))
    );
};

export const mapValuesToOptions = values =>
    (values.every(isValueOption) && values) ||
    values.map(value => ({ code: value.toLowerCase(), name: value }));

export const isVisible = (condition = true) => (condition ? undefined : false);

export const stateDecoratorFactory = (valueName, callbackName) => (
    story,
    { args }
) => {
    const argsValue = args[valueName];
    const [value, setValue] = useState(argsValue);

    const onValueChange = newValue => {
        setValue(newValue);

        args[callbackName](newValue);
    };

    const isFirstRun = useRef(true);
    useEffect(() => {
        if (isFirstRun.current) {
            isFirstRun.current = false;
            return;
        }
        setValue(argsValue);
    }, [argsValue]);

    return cloneElement(story(), {
        [valueName]: value,
        [callbackName]: onValueChange,
    });
};
